import request from "@/utils/request/ha";

function buildHAUrl(api) {
  if (process.env.VUE_APP_MODE === "addon") {
    return api;
  }
  return `/api-ha/${window.sessionStorage.getItem("hassUrl")}${api}`;
}

// api for ha
export function testAPI({ url, accessToken }) {
  let haUrlHeader = `/api-ha/${url}`;
  if (process.env.VUE_APP_MODE === "addon") {
    haUrlHeader = "";
  }
  return request({
    headers: {
      Authorization: "Bearer " + accessToken,
    },
    url: `${haUrlHeader}/api/`,
    method: "get",
  });
}

export function getKoolsmartCfg() {
  // koolsmart作为automation的id，约定标识
  return request({
    url: buildHAUrl("/api/config/automation/config/koolsmart"),
    method: "get",
  });
}

export function createKoolsmartCfg() {
  return request({
    url: buildHAUrl("/api/config/automation/config/koolsmart"),
    method: "post",
    data: {
      alias: "koolsmart",
      trigger: [],
      action: [
        {
          variables: {
            projects: [],
            devices: [],
          },
        },
      ],
    },
  });
}

export function updateKoolsmartCfg(projects) {
  return request({
    url: buildHAUrl("/api/config/automation/config/koolsmart"),
    method: "post",
    data: {
      alias: "koolsmart",
      trigger: [],
      action: [
        {
          variables: {
            projects: projects,
            devices: [],
          },
        },
      ],
    },
  });
}

export function getSceneMode({ uid }) {
  return request({
    url: buildHAUrl(`/api/config/scene/config/${uid}`),
    method: "get",
  });
}

// uid: koolsmart_1997244878577_1697444878779
export function updateSceneMode({ uid, name, icon, entities }) {
  return request({
    url: buildHAUrl(`/api/config/scene/config/${uid}`),
    method: "post",
    data: {
      name,
      icon,
      entities,
    },
  });
}

export function deleteSceneMode({ uid }) {
  return request({
    url: buildHAUrl(`/api/config/scene/config/${uid}`),
    method: "delete",
  });
}
