import Vue from "vue";
import VueRouter from "vue-router";
import Layout from "@/layout";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    redirect: "/login",
  },
  {
    name: "TestView",
    path: "/test",
    component: () => import("../views/TestView/index.vue"),
  },
  {
    name: "LoginView",
    path: "/login",
    component: () => import("../views/LoginView/index.vue"),
  },

  {
    path: "/project",
    component: Layout,
    redirect: "/project/index",
    children: [
      {
        name: "PublishView",
        path: "/project/publish",
        component: () => import("../views/PublishView/index.vue"),
      },
      {
        name: "SceneView",
        path: "/project/scene",
        component: () => import("../views/SceneView/index.vue"),
      },
      {
        name: "EditSceneView",
        path: "/project/scene/edit",
        component: () => import("../views/EditSceneView/index.vue"),
      },
      {
        name: "ViewpointDesignView",
        path: "/project/viewpoint-design",
        component: () => import("../views/ViewpointDesignView/index.vue"),
      },
      {
        name: "EditViewpointView",
        path: "/project/viewpoint-design/edit",
        component: () => import("../views/EditViewpointView/index.vue"),
      },
      {
        name: "TestDesignView",
        path: "/project/test-design",
        component: () => import("../views/TestDesignView/index.vue"),
      },
      {
        name: "ProjectView",
        path: "/project/index",
        component: () => import("../views/ProjectView/index.vue"),
      },
      {
        name: "ScenarioStartView",
        path: "/project/scenario-start",
        component: () => import("../views/ScenarioStartView/index"),
      },
      {
        name: "ScenarioEndView",
        path: "/project/scenario-end",
        component: () => import("../views/ScenarioEndView/index"),
      },
    ],
  },
];

const router = new VueRouter({
  mode: "hash",
  base: process.env.BASE_URL,
  routes,
});

export default router;
