const savedStore = JSON.parse(window.localStorage.getItem("localStore"));
const initialState = {
  editViewpoint: undefined,
};
export default {
  namespaced: true,
  state: savedStore?.viewpointStore ?? initialState,
  mutations: {
    setEditViewpoint(state, viewpoint) {
      state.editViewpoint = viewpoint;
    },
  },
};
