const savedStore = JSON.parse(window.localStorage.getItem("localStore"));
const initialState = {
  projects: [],
  selectedProject: undefined,
};
export default {
  namespaced: true,
  state: savedStore?.projectStore ?? initialState,
  mutations: {
    setProjects(state, projects) {
      state.projects = projects;
    },
    setSelectedProject(state, project) {
      state.selectedProject = project;
    },
    addProject(state, project) {
      state.projects.push(project);
    },
    setProjectScenes(state, scenes) {
      state.selectedProject.scenes = scenes;
    },
  },
};
