<template>
  <div class="all">
    <div class="header">
      <el-radio-group v-model="step">
        <el-radio-button plain @click.native.prevent="toProject" :label="1">
          <div class="icon-text">
            <i class="icon iconfont icon-wenjianwenjianjia"></i>
            <span
              >Project<br />
              selection</span
            >
          </div>
        </el-radio-button>
        <span class="el-icon-arrow-right arrow"></span>

        <el-tooltip
          class="item"
          effect="dark"
          content="Please add or click on an existing floor plan"
          placement="top"
        >
          <el-radio-button
            plain
            @click.native.prevent="toScenarioEditor"
            :label="2"
          >
            <div class="icon-text">
              <i class="icon iconfont icon-xuanxiang"></i>
              <span
                >Scene<br />
                editor</span
              >
            </div>
          </el-radio-button>
        </el-tooltip>
        <span class="el-icon-arrow-right arrow"></span>

        <el-tooltip
          class="item"
          effect="dark"
          content="Please select a scenario"
          placement="top"
          :disabled="!autoState"
        >
          <el-radio-button
            plain
            @click.native.prevent="toHomeDesign"
            :label="3"
          >
            <div class="icon-text">
              <i class="icon iconfont icon-ic-zbzzkk"></i>
              <span
                >Viewpoint<br />
                design</span
              >
            </div>
          </el-radio-button>
        </el-tooltip>

        <span class="el-icon-arrow-right arrow"></span>
      </el-radio-group>
      <el-button plain @click="publish2">
        <div class="icon-text">
          <i class="icon iconfont icon-fabu"></i>
          <span>Publish</span>
        </div>
      </el-button>
    </div>
    <el-tooltip
      class="item"
      effect="light"
      content="help documentation"
      placement="right"
    >
      <i class="mdi mdi-help-circle prompt" @click="toHelp"></i>
    </el-tooltip>
  </div>
</template>

<script>
export default {
  name: "HeaderBar",
  data() {
    return {
      ws: null,
      projects: "",
      ha_url: window.sessionStorage.getItem("hass_url"),
      configId: "",
      instance: "",
    };
  },
  created() {
    this.ws = this.$store.state.webSocket;
    this.configId = this.$store.state.configId;
    this.instance = this.$store.state.instance;
  },
  computed: {
    step() {
      return this.$store.state.step;
    },
    scenarioState() {
      return this.$store.state.scenarioState;
    },
    autoState() {
      return this.$store.state.autoState;
    },
  },
  watch: {
    "$store.state.step": function (newVal) {
      this.step = newVal;
      if (this.step == 1) {
        this.toProject();
      } else if (this.step == 2) {
        this.toScenario();
      } else if (this.step == 3) {
        this.toAutomated();
      }
    },
    "$store.state.scenarioState": function (newVal) {
      this.scenarioState = newVal;
    },
    "$store.state.autoState": function (newVal) {
      this.autoState = newVal;
    },
  },
  methods: {
    toHelp() {
      window.open(this.$store.state.helpAll, "_blank");
    },
    toProject() {
      if (this.$route.path !== "/project/index") {
        this.$router.push("/project/index");
      }
    },
    toScenarioEditor() {
      // if (this.scenarioState === true) return;
      this.$store.commit("setShow", "1");
      if (this.$route.path !== "/project/scene") {
        this.$router.push("/project/scene");
      }
    },
    toHomeDesign() {
      // if (this.autoState == true) return;
      if (this.$route.path !== "/project/viewpoint-design") {
        this.$router.push("/project/viewpoint-design");
      }
      this.$store.commit("setShow", "3");
      // if (this.$store.state.selectScenarioName != "") {
      //   this.$store.commit("setShow", "3");
      // } else {
      //   this.$message({
      //     type: "error",
      //     message: "Please select a scenario first!",
      //   });
      // }
    },

    createDash() {
      // init dashboard
      this.$store.commit("setWsId");
      let createDash = {
        type: "lovelace/dashboards/create",
        mode: "storage",
        require_admin: false,
        show_in_sidebar: true,
        title: "KoolSmart",
        url_path: "dashboard-koolsmart",
        id: this.$store.state.wsId,
      };
      this.ws.send(JSON.stringify(createDash));
      this.$store.commit("setWsId");
    },
    publish2() {
      if (this.$route.path !== "/project/publish") {
        this.$router.push("/project/publish");
      }
    },
    async publish() {
      this.$store.dispatch("saveConfig", this);
      this.projects = JSON.parse(this.$store.state.projects);
      let automations = ["automation.koolsmart"];
      this.createDash();
      let dashboards = {
        title: "My Home",
        views: [],
      };
      let projectsList = Object.keys(this.projects);

      for (let i = 0; i < projectsList.length; i++) {
        // create view
        let scenarioList = this.projects[projectsList[i]].scenarioList;
        dashboards.views.push({
          path: projectsList[i],
          title: projectsList[i],
          type: "sidebar",
          cards: [
            {
              type: "vertical-stack",
              cards: [],
              view_layout: { position: "sidebar" },
            },
            {
              type: "vertical-stack",
              cards: [],
              view_layout: { position: "main" },
            },
          ],
        });
        let cardsEntity = dashboards.views[i]["cards"][0].cards;
        let cardsPic = dashboards.views[i]["cards"][1].cards;

        for (let j = 0; j < scenarioList.length; j++) {
          // create element
          let element = [];
          let scenario = scenarioList[j];
          let icons =
            this.projects[projectsList[i]].scenarios[scenario].add_icons;
          for (let iconEntity in icons) {
            if (icons[iconEntity].show_img == false) {
              element.push({
                type: "state-icon",
                entity: icons[iconEntity].id,
                icon: "mdi:" + icons[iconEntity].icon.slice(8),
                style: {
                  left: icons[iconEntity].left_percent,
                  top: icons[iconEntity].top_percent,
                  width: "30px",
                  height: "30px",
                },
              });
            } else {
              await this.createGSCanvas(icons[iconEntity].img).then((res) => {
                console.log(res);
                element.push({
                  type: "image",
                  entity: icons[iconEntity].id,
                  style: {
                    left: icons[iconEntity].left_percent,
                    top: icons[iconEntity].top_percent,
                    width: "30px",
                    height: "30px",
                  },
                  state_image: {
                    off: res,
                    on: icons[iconEntity].img.slice(21),
                  },
                });
              });
            }
          }
          cardsPic.push({
            type: "conditional",
            conditions: [
              {
                entity:
                  "automation." +
                  this.projects[projectsList[i]].scenarios[scenario]
                    .automation_name,
                state: "on",
              },
            ],
            card: {
              type: "picture-elements",
              elements: element,
              image: this.projects[projectsList[i]].scenarios[scenario].pic,
            },
          });
          cardsEntity.push({
            type: "entities",
            entities: [
              "automation." +
                this.projects[projectsList[i]].scenarios[scenario]
                  .automation_name,
            ],
            state_color: true,
          });
          automations.push(
            "automation." +
              this.projects[projectsList[i]].scenarios[scenario].automation_name
          );
          console.log(dashboards);
        }
      }
      let saveDashboard = {
        type: "lovelace/config/save",
        url_path: "dashboard-koolsmart",
        config: dashboards,
        id: this.$store.state.wsId,
      };
      this.ws.send(JSON.stringify(saveDashboard));
      this.$store.commit("setWsId");
      //Close all scenarios
      let turn_off = {
        type: "call_service",
        domain: "automation",
        service: "turn_off",
        service_data: {
          entity_id: automations,
        },
        id: this.$store.state.wsId,
      };
      this.$store.commit("setWsId");
      this.ws.send(JSON.stringify(turn_off));
      this.$message({
        type: "success",
        message: "Published successfully!",
      });
    },
    createGSCanvas(imgUrl) {
      var img = document.createElement("img");
      img.src = imgUrl;
      img.crossOrigin = "";
      img.setAttribute("width", "30");
      img.setAttribute("height", "30");
      // Create a new canvas element and draw a colored image in its drawing context.
      var canvas = document.createElement("canvas");
      canvas.width = 30;
      canvas.height = 30;
      var ctx = canvas.getContext("2d");
      return new Promise((resolve) => {
        img.onload = function () {
          ctx.drawImage(img, 0, 0, 30, 30);
          // Obtain the original image data
          var c = ctx.getImageData(0, 0, img.width, img.height);
          // Iterate through each element in the loop.
          // Calculate the average value of the red, green, and blue color components for each colored pixel to obtain the grayscale value of the color.
          for (let i = 0; i < c.height; i++) {
            for (let j = 0; j < c.width; j++) {
              var x = i * 4 * c.width + j * 4;
              var r = c.data[x];
              var g = c.data[x + 1];
              var b = c.data[x + 2];
              c.data[x] = c.data[x + 1] = c.data[x + 2] = (r + g + b) / 3;
            }
          }
          // Put the grayscale data back into the drawing context of the canvas.
          ctx.putImageData(c, 0, 0, 0, 0, c.width, c.height);
          // Return the original image data as the source for the new grayscale image.
          let result = canvas.toDataURL();
          canvas.remove();
          img.remove();
          resolve(result);
        };
      });
    },
  },
};
</script>

<style lang="less" scoped>
@import "./styles/headerBar.less";
</style>
