const savedStore = JSON.parse(window.localStorage.getItem("localStore"));
const initialState = {
  editScene: undefined,
  scenes: [],
};
export default {
  namespaced: true,
  state: savedStore?.sceneStore ?? initialState,
  mutations: {
    setEditScene(state, scene) {
      state.editScene = scene;
    },
    setScenes(state, scenes) {
      state.scenes = scenes;
    },
  },
};
