import request from "@/utils/request/koolsmart";

// api for koolsmart
const URL_MAP = {
  coohom: {
    login: "api-ks/api/account/oauth/token",
    getDesignList: "api-ks/koolsmart/api/site/design/list",
    getDesignRenderpics: "api-ks/koolsmart/api/site/design/designrenderpics",
  },
  kujiale: {
    login: "api-kjl/koolsmart/api/site/user/login",
    getDesignList: "api-kjl/koolsmart/api/site/design/list",
    getDesignRenderpics: "api-kjl/koolsmart/api/site/design/designrenderpics",
  },
};

// TODO: password是未加密的
export function login({ email, password, grantType }, accountType) {
  const apiUrl = URL_MAP[accountType].login;
  return request({
    url: apiUrl,
    method: "post",
    data: {
      email,
      password,
      grant_type: grantType,
    },
  });
}

export function getDesignList({ start, num }, accountType) {
  const apiUrl = URL_MAP[accountType].getDesignList;
  return request({
    url: apiUrl,
    method: "get",
    params: {
      start,
      num,
    },
  });
}

export function getDesignRenderpics({ obsDesignId }, accountType) {
  const apiUrl = URL_MAP[accountType].getDesignRenderpics;
  return request({
    url: apiUrl,
    method: "get",
    params: {
      obsDesignId,
    },
  });
}
