import Vue from "vue";
import Vuex from "vuex";
import haStore from "./ha.store";
import sceneStore from "./scene.store";
import koolsmartStore from "./koolsmart.store";
import projectStore from "./project.store";
import viewpointStore from "./viewpoint.store";
// import haAdapterStore from "./haAdapter.store";
Vue.use(Vuex);

const storagePlugin = (store) => {
  // 监听每次 mutation 的提交
  store.subscribe((mutation, state) => {
    // 将 store 数据保存到本地id
    window.localStorage.setItem("localStore", JSON.stringify(state));
  });
};

export default new Vuex.Store({
  modules: {
    haStore,
    sceneStore,
    koolsmartStore,
    projectStore,
    viewpointStore,
  },
  plugins: [storagePlugin],
  state: {
    // Device manufacturer name(Can be expanded)
    manufacturer: ["xiaomi_miot"],
    // HA icons(Can be expanded)
    icons: [
      "home",
      "lamp",
      "lamps",
      "ceiling-light",
      "chandelier",
      "curtains",
      "curtains-closed",
      "desk-lamp",
      "door",
      "fan",
      "fire",
      "hanger",
      "kettle",
      "lock",
      "motion-sensor",
      "power",
      "speaker",
      "thermometer",
      "toilet",
      "video",
      "water",
      "window-open-variant",
      "access-point",
      "alarm-check",
      "archive",
      "bluetooth",
      "camera-wireless",
      "car-outline",
      "cat",
      "dog",
      "email-outline",
      "emoticon-outline",
      "face-man-profile",
      "face-woman-profile",
      "key",
      "play-circle",
      "presentation-play",
      "signal-variant",
      "sleep",
      "snowflake",
      "wifi",
    ],
    helpAll:
      "https://kloudsim-cos.kujiale.com/KoolSmart/ExcellentDesignCases/koolsmart-en.pdf",
    helpIcon:
      "https://kloudsim-cos.kujiale.com/KoolSmart/ExcellentDesignCases/icon.pdf",
    // Control subpage display
    show: "1",
    // obsDesignId
    planId: "",
    // Configuration file id (Saved file id)
    configId: "",
    // Master configuration file (save)
    projects: "{}",
    // Master devices information (save)
    devices: "{}",
    // Currently displayed project name
    projectName: "",
    // Currently selected device name
    deviceName: "",
    // Currently selected device id
    deviceId: "",
    // Currently selected scenario name
    selectScenarioName: "",
    // Information of the entity being dragged
    moveEntityId: "",
    moveEntityShow: "",
    moveEntityIcon: "",
    moveEntityImg: "",
    // Global websocket
    webSocket: null,
    // websocket id must be incremented
    wsId: 1,
    // Control the heartbeat mechanism of the websocket
    lockReconnect: false,
    //
    automationId: "",

    // is(not) first login
    firstLog: true,
    // is(not) visitor mode
    isVisitor: false,
    // Control header
    step: 1,
    // Control the display of the header bar
    scenarioState: true,
    autoState: true,
    // Whether the websocket is connected successfully
    connect: false,
    // visitor data
    visitor: {
      obsPlanId: "3FO405QJNEEP",
      pic: "https://qhtbdoss.kujiale.com/fpimgnew/prod/bim/3FO405QJNEEP/1/MTM7LCIKTIERAAABAAAAABY8.jpg",
      designName: "Home Design",
      realArea: "87",
      renderPics: [
        "https://qhrenderpicoss.kujiale.com/r/2023/08/14/L3D541S313B25ENDPHDA37QUWIDJILUF3P3XQ888_3840x2160.jpg",
        "https://qhrenderpicoss.kujiale.com/r/2023/08/14/L3D541S313B25ENDPHDA35YUWI3AYLUF3P3WS888_3840x2160.jpg",
        "https://qhrenderpicoss.kujiale.com/r/2023/08/14/L3D541S313B25ENDPHDA3WIUWIDJILUF3P3XM888_3840x2160.jpg",
        "https://qhrenderpicoss.kujiale.com/r/2023/08/14/L3D541S313B25ENDPHDA4IIUWLWVYLUF3P3WO888_3840x2160.jpg",
      ],
    },
    // Trigger save
    save: false,
    // axios instance
    instance: null,
  },
  getters: {},
  actions: {
    // Save every 10 seconds.
    autoSave(context, vm) {
      setInterval(() => {
        context.dispatch("saveConfig", vm);
      }, 10000);
    },
    saveConfig({ state }, vm) {
      let actions = [
        {
          variables: {
            projects: state.projects,
            devices: state.devices,
          },
        },
      ];
      let config = {
        alias: "KoolSmart",
        description: "",
        trigger: [],
        condition: [],
        action: actions,
        mode: "single",
      };
      let instance = vm.$axios.create({
        headers: {
          Authorization:
            "Bearer " + window.sessionStorage.getItem("accessToken"),
          "Content-Type": "application / json",
          "Access-Control-Allow-Origin": "*",
        },
      });
      instance.post(
        "/api-ha/api/config/automation/config/" + state.configId,
        config
      );
    },
  },
  mutations: {
    checkConnection(state, data) {
      let e = data.param1;
      let vm = data.param2;
      if (e.data.includes("auth_invalid")) {
        vm.$notify.error({
          title: "error",
          message:
            "Because of network issues, please refresh the web page, and click to Keep me logged in",
        });
        vm.$store.commit("setConnect", false);
      }
    },
    initWebsocket(state, vm) {
      function init() {
        try {
          state.webSocket = new WebSocket(
            "ws://" +
              window.sessionStorage.getItem("hass_url").slice(7) +
              "/api/websocket"
          );
        } catch (err) {
          vm.$notify.error({
            title: "error",
            message:
              "Because of network issues, please refresh the web page, and click to Keep me logged in",
          });
        }
        state.webSocket.onopen = function () {
          console.log("start to connect ha websocket");
          state.webSocket.send(
            JSON.stringify({
              type: "auth",
              access_token: window.sessionStorage.getItem("ha_token"),
            })
          );

          console.log("ha websocket connection success");
        };
        state.webSocket.onmessage = function (e) {
          vm.$store.commit("setConnect", true);
          console.log("received data:", e.data);
          if (e.data.includes("auth_invalid")) {
            vm.$notify.error({
              title: "error",
              message:
                "Because of network issues, please refresh the web page, and click to Keep me logged in",
            });
            vm.$store.commit("setConnect", false);
          }
        };
        state.webSocket.onerror = function () {
          console.log("connection error");
          reconnect(
            "ws://" +
              window.sessionStorage.getItem("hass_url").slice(7) +
              "/api/websocket"
          );
        };
        state.webSocket.onclose = function () {
          console.log("connection close");
          reconnect(
            "ws://" +
              window.sessionStorage.getItem("hass_url").slice(7) +
              "/api/websocket"
          );
        };
      }
      function reconnect(url) {
        if (state.lockReconnect) return;
        state.lockReconnect = true;
        setTimeout(() => {
          init();
          state.lockReconnect = false;
        }, 3000);
      }
      init();

      state.wsId = new Date().getTime();
      state.instance = vm.$axios.create({
        headers: {
          Authorization: "Bearer " + window.sessionStorage.getItem("ha_token"),
          "Content-Type": "application / json",
          "Access-Control-Allow-Origin": "*",
        },
      });
    },
    setScenarioState(state, value) {
      state.scenarioState = value;
    },
    setAutoState(state, value) {
      state.autoState = value;
    },
    setSelectScenarioName(state, name) {
      state.selectScenarioName = name;
    },
    setShow(state, value) {
      state.show = value;
    },
    setPlanId(state, id) {
      state.planId = id;
    },
    setProjects(state, value) {
      state.projects = value;
    },
    setProjectName(state, name) {
      state.projectName = name;
    },
    setDeviceName(state, name) {
      state.deviceName = name;
    },
    setDeviceId(state, id) {
      state.deviceId = id;
    },
    setMoveEntityId(state, id) {
      state.moveEntityId = id;
    },
    setMoveEntityIcon(state, icon) {
      state.moveEntityIcon = icon;
    },
    setMoveEntityImg(state, img) {
      state.moveEntityImg = img;
    },
    setMoveEntityShow(state, status) {
      state.moveEntityShow = status;
    },
    setDevices(state, value) {
      state.devices = value;
    },
    setAutomationId(state, id) {
      state.automationId = id;
    },
    setConfigId(state, id) {
      state.configId = id;
    },
    setWsId(state, id) {
      // The ID must be incremented.
      state.wsId += 10000;
    },
    setFirstLog(state, value) {
      state.firstLog = value;
    },
    setIsVisitor(state, value) {
      state.isVisitor = value;
    },
    setStep(state, value) {
      state.step = value;
    },
    setConnect(state, status) {
      state.connect = status;
    },
    setSave(state, status) {
      state.save = status;
    },
  },
});
