import {
  getAuth,
  createConnection,
  ERR_HASS_HOST_REQUIRED,
  subscribeEntities,
  subscribeConfig,
} from "home-assistant-js-websocket";
import { testAPI } from "@/services";

const savedStore = JSON.parse(window.localStorage.getItem("localStore"));
const initialState = {
  manufacturers: ["xiaomi_miot", "tuya"],
  // manufacturers: ["xiaomi_miot"],
  haWebsocket: undefined,
  connectSuccess: false,
  selectedProject: undefined,
  entitiesFromSubscribe: [],
  entityRegistry: [],
  deviceRegistry: [],
};
export default {
  namespaced: true,
  state: savedStore?.haStore ?? initialState,
  // getters: {
  //   scenes: (state) => {
  //     let scenes = [];
  //     for (let entity in state.entitiesFromSubscribe) {
  //       if (entity.split(".")[0] === "scene") {
  //         scenes.push(state.entitiesFromSubscribe[entity]);
  //       }
  //     }
  //     return scenes;
  //   },
  //   entities: (state) => {
  //     return state.entityRegistry.filter((entity) =>
  //       state.manufacturers.includes(entity.platform)
  //     );
  //   },
  //   devices: (state) => {
  //     return state.deviceRegistry.filter((device) =>
  //       state.manufacturers.includes(device.identifiers[0][0])
  //     );
  //   },
  // },
  mutations: {
    setSelectedProject(state, project) {
      state.selectedProject = project;
    },
    setManufacturers(state, manufacturers) {
      state.manufacturers = manufacturers;
    },

    SET_CONNECT_SUCCESS(state, success) {
      state.connectSuccess = success;
    },
    SET_WEBSOCKET(state, websocket) {
      state.haWebsocket = websocket;
    },
    SET_ENTITIES_FROM_SUBSCRIBE(state, entitiesFromSubscribe) {
      state.entitiesFromSubscribe = entitiesFromSubscribe;
    },
    SET_ENTITY_REGISTRY(state, entityRegistry) {
      state.entityRegistry = entityRegistry;
    },
    SET_DEVICE_REGISTRY(state, deviceRegistry) {
      state.deviceRegistry = deviceRegistry;
    },
  },
  actions: {
    async createHAWebsocket(
      { commit, dispatch },
      { hassUrl, accessToken, vm }
    ) {
      commit("setManufacturers", ["xiaomi_miot", "tuya"]);
      // get hassTokens after HA persistent login

      // let hassTokens = JSON.parse(window.localStorage.getItem("hassTokens"));
      let auth;
      let hassTokens = {
        access_token: accessToken,
        token_type: "Bearer",
        hassUrl: hassUrl,
        clientId: hassUrl,
      };
      try {
        auth = await getAuth({
          hassUrl,
          loadTokens: () => hassTokens,
        });
      } catch (err) {
        vm.$notify.error({
          title: "error",
          message: err,
        });
        return;
      }
      const haWebsocket = await createConnection({ auth }).then(
        (res) => {
          return res;
        },
        (err) => {
          vm.$notify.error({
            title: "Websocket Connect Err",
            message: "Please check your Home Assistant",
          });
          console.log("Home Assistant Websocket Err", err);
        }
      );
      // You can test if the connection is successful
      // subscribeEntities(haWebsocket, (ent) => console.log("entities", ent));
      commit("SET_WEBSOCKET", haWebsocket);
      if (haWebsocket) {
        dispatch("getEntityRegistry");
        dispatch("getDeviceRegistry");
        subscribeEntities(haWebsocket, (entities) =>
          commit("SET_ENTITIES_FROM_SUBSCRIBE", entities)
        );
        // dispatch("testWebsocket");
      }

      const testAPIRes = await testAPI({ url: hassUrl, accessToken }).then(
        (res) => {
          commit("SET_CONNECT_SUCCESS", true);
          return true;
        },
        (err) => {
          vm.$notify.error({
            title: "REST API Test Err",
            message: "Please check your Home Assistant",
          });
          return false;
        }
      );
      return testAPIRes;
    },
    getEntityRegistry({ commit, dispatch, state }) {
      let getEntitiestRequestCfg = {
        type: "config/entity_registry/list",
      };
      state.haWebsocket
        .sendMessagePromise(getEntitiestRequestCfg)
        .then((res) => {
          commit("SET_ENTITY_REGISTRY", res);
        });
    },
    getDeviceRegistry({ commit, dispatch, state }) {
      let getDevicesRequestCfg = {
        type: "config/device_registry/list",
      };
      state.haWebsocket.sendMessagePromise(getDevicesRequestCfg).then((res) => {
        commit("SET_DEVICE_REGISTRY", res);
      });
    },
  },
};
