<template>
  <div id="app">
    <router-view />
  </div>
</template>
<script>
export default {
  created() {
    // loading ha connection config
    let haSettingLocal = this.getHaSettingFromLocal();
    if (!haSettingLocal.hassUrl || !haSettingLocal.accessToken) {
      this.$notify.error({
        title: "error",
        message: "Please complete Home Assistant setting first.",
      });
    } else {
      this.$store
        .dispatch("haStore/createHAWebsocket", {
          hassUrl: haSettingLocal.hassUrl,
          accessToken: haSettingLocal.accessToken,
          vm: this,
        })
        .then((res) => {
          window.sessionStorage.setItem("hassUrl", haSettingLocal.hassUrl);
          window.sessionStorage.setItem(
            "accessToken",
            haSettingLocal.accessToken
          );
        });
    }
  },
  methods: {
    getHaSettingFromLocal() {
      let haSettingLocal = { hassUrl: "", accessToken: "" };
      let hassTokens = JSON.parse(window.localStorage.getItem("hassTokens"));
      if (hassTokens) {
        haSettingLocal.hassUrl = hassTokens["hassUrl"] || "";
        haSettingLocal.accessToken = hassTokens["access_token"] || "";
      }
      return haSettingLocal;
    },
  },
};
</script>

<style lang="less">
body {
  background-color: #e7eef7;
  overflow: hidden;
}
</style>
